body {
    margin: 0;
    margin-bottom: 35px;
    padding: 0;
}

.main-footer {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(45, 81, 189);
    color: white;
    text-align: center;
}

.main_note {
    display: flex;
    flex-direction: row;
    background-color: red;
}

.titleinput {
    font-size: larger;
    border: none;
    outline: none;
}

textarea {
    outline: none;
    border: none;
    resize: none;
}

.plus-sign {
    color: white;
    border-radius: 50%;
}

.alignButton {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
}